.intro.section {
  // position: fixed;
  // top: 0;
  // left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  background: #fff;
  color: #000;
}
.section {
  color: white;
  .bg {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: -2;
    overflow: hidden;
    &-screen {
      z-index: -1;
      position: absolute;
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.4);

      &.darker {
        background: rgba(0, 0, 0, 0.6);
      }

      &.lighter {
        background: rgba(0, 0, 0, 0.3);
      }

      &.purple {
        background: rgba(55, 33, 79, 0.6);
      }

      &.black {
        background: #000;
      }
    }
  }
  .inner-frame {
    // min-height: 100vh;
    height: 100%;
    padding: 100px 60px 185px 60px;

    &.centered {
      padding: 185px 60px;
    }
  }

  .slide-title h2 {
    font-size: 30px;
    font-weight: 500;
  }
  .slide-subtitle h3 {
    font-size: 20px;
    font-weight: 200;
    color: rgba(255, 255, 255, 0.75);
    margin-bottom: 0;
  }

  @media screen and (min-width: $medium) {
    .slide-title h2 {
      font-size: 3.75vw;
    }
    .slide-subtitle h3 {
      font-size: 2.5vw;
    }
  }
}

.drawer-button {
  cursor: pointer;
  width: 80px;
  margin-bottom: 0px;

  &_container {
    position: relative;
    display: flex;
    flex-direction: column;
    text-align: center;

    position: absolute;
    transform: translateX(-50%);
    left: 50%;
    bottom: 0;
    margin-bottom: 60px;
    z-index: 10003;

    img {
      transition: all ease 300ms;
      transform-origin: 45% center;
    }
  }
}
.drawer-close-button {
  opacity: 0;
  position: absolute;
  pointer-events: none;
  top: 0;
  left: 0;
  width: 80px;
  transform: rotate(90deg);
}

.drawer-open .drawer-button_container {
  .drawer-button_label {
    opacity: 0;
  }
  img {
    opacity: 0;
  }
  .drawer-close-button {
    transform: rotate(0);
    opacity: 1;
  }
}

.leftDrawer {
  background: #fff;
  color: #000;
  height: 100vh;
  position: absolute;
  left: 0;
  top: 0;
  transform: translateX(-100%);
  width: 50%;
  z-index: 10002;
  padding: 0 30px;

  overflow-y: auto;

  .drawer-open & {
    box-shadow: black 0px 0px 50px 0px;
  }

  .drawer-title {
    color: $orange;
  }
  p {
    line-height: 1.6;
  }

  hr {
    border-color: #ddd;
    border-width: 1px;
  }
}

[data-section='0'] {
  background-color: inherit !important;
}

// ceo
[data-section='2'] {
  .inner-frame .grid-y {
    max-width: 1000px;
  }
  .ceo-sub {
    font-size: 18px;
    font-weight: 700;
  }

  .ceo-h3 {
    font-size: 20px;
    font-weight: 500;
  }
}

.learnmore {
  background: white;
  color: #000;
  h2 {
    color: $orange;
  }
  a > img {
    width: 30px;
    height: 30px;
  }
}

